import Vue from 'vue'
import Router from 'vue-router'

Vue.use(Router)

export default new Router({
  routes: [
    {
      path: '/',
      name: 'index',
      component: () => import('./views/Index.vue'),
      children: [
        { path: '/intro', name: 'intro', component: () => import('./components/Intro.vue') },
        { path: '/about', name: 'about', component: () => import('./components/About.vue') },
        { path: '/skills', name: 'skills', component: () => import('./components/Skills.vue') },
        { path: '/works', name: 'works', component: () => import('./components/Works.vue') },
        { path: '/book', name: 'book', component: () => import('./components/Book.vue') },
        { path: '/books', name: 'books', component: () => import('./components/Books.vue') },
      ]
    }
  ]
})
